.nav {
    grid-area: nav;
    background-color: #f3eded;
    border-radius: 10px 10px 0 0;
}


.item {
    margin: 0 0 0 0.5em;
}

.item a {
    color: #000;
    text-decoration: none;
    font-size: 18px;
}

.item a.activeLink {
    color: #df889a;
}

.activeFriends {
    margin: 2em 0 0 0;
}

.title {
    margin: 0 0 1em 0.5em;
}